import debug from 'debug';
import loggly from 'loggly';
import { format } from 'util';
import strip from 'strip-color';

import { LOGGLY_CUSTOMER_TOKEN } from '../assets/environment';

export default function sendDebugToLoggly({ sessionId }) {
  const client = loggly.createClient({
    token: LOGGLY_CUSTOMER_TOKEN,
    subdomain: 'hichroma',
    tags: ['storybook-chromatic'],
    json: true,
  });

  // Is the user debugging already? If so we just pass through what they are debugging
  const isDebugging = !!process.env.DEBUG;
  if (!isDebugging) {
    // Turn on debugging for everything
    process.env.DEBUG = '*,-babel';
  }

  console.log('setting up debug.log');
  debug.log = (...args) => {
    const msg = strip(format(...args));
    console.log('sending to loggly', msg);
    client.log({ sessionId, msg });
    if (isDebugging) {
      console.log(...args);
    }
  };
}



